.item {
  border: 1px solid #ccc;
  padding: 10px;
  margin: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  img {
    max-width: 100px;
    max-height: 100px;
    margin: 10px 0;
  }

  div {
    font-weight: bold;
    margin: 5px 0;
  }
}
